#id{
  background-color: red;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
}

.controlpin {
background-image: url("data:image/png;base64,");
  width: 330px;
  height: 45px;
  background-repeat: no-repeat;
  position: absolute;
  left: 0; 
  right: 0; 
  margin-left: auto; 
  margin-right: auto; 
  z-index: 10;
  top: -4px;
  opacity: 0.9;
  border-radius: 0px 0px 10px 10px;
border: 0px solid #000;
background: #161616;
box-shadow: 0px 0px 0.2px 3px rgba(0, 0, 0, 0.25);
}

.controlpin:hover{
  opacity: 1;
}

.controller {
  width: 335px;
  height: 175px;
  flex-shrink: 0;
  border-radius: 0px 0px 10px 10px;
  border: 0px solid #000;
  background: #fff;
  box-shadow: 0px 0px 0.2px 3px rgba(0, 0, 0, 0.25);
  position: absolute;
  visibility: hidden;
  left: 0; 
  right: 0; 
  margin-left: auto; 
  margin-right: auto; 
  z-index: 8;
  padding: 10px;
}


